import { Component, OnInit } from '@angular/core';
import {PlaylistService} from '../../../shared/playlist.service';
import {Playlist} from '../../../models/playlist';
import {VideoService} from '../../../shared/video.service';
import {Video} from '../../../models/video';
import {Helper} from '../../../common/helper';
declare var jquery: any;
declare var $: any;
@Component({
  selector: 'app-browse',
  templateUrl: './browse.component.html'
})
export class BrowseComponent implements OnInit {

  playlists: any = null;
  isLoadedPlaylist = false;
  isLoadedTracks = false;
  country = 'US';
  query: string = 'top 100';
  videoList: any = null;
  constructor(private playlistService: PlaylistService, private videoService: VideoService, private helper: Helper) { }

  ngOnInit() {
    this.query = localStorage.getItem('search');
    this.search(this.query);
    this.helper.makeMeta('Browse');
  }

  search(query: string) {
    this.getFromLocal(query);
      this.playlistService.fetchPlaylistYoutube(this.country, this.query)
          .subscribe(
          data => {
              this.playlists = data.items.map(
                  item => {
                      return new Playlist('',
                          item.id.playlistId,
                          item.snippet.title,
                          item.snippet.thumbnails.high.url ,
                          item.snippet.channelTitle
                      );
                  });
                this.isLoadedPlaylist = true;
          });

    this.videoService.fetchVideos(this.query)
        .toPromise()
        .then(data => {
            this.videoList = data.items.map(item => {
                return new Video(
                    item.id.videoId,
                    item.snippet.title,
                    item.snippet.thumbnails.high.url,
                    item.snippet.description, '', '', item.snippet.channelTitle);
            });
            this.isLoadedTracks = true;
        });
  }

  changePlaylist(playListCode: string) {
    this.helper.changePlaylist(playListCode);
  }
  showPlaylist() {
    $("#playlist-area").show();
    $("#track-area").hide();
  }

  showTracks(){
    $("#playlist-area").hide();
    $("#track-area").show();
  }


  getFromLocal(query: string) {
    if (localStorage.getItem("search") === null || query === '') {
      localStorage.setItem('search', '');
    } else {
      localStorage.setItem('search', query);
    }
    this.query = localStorage.getItem('search');
  }

}



