import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';

@Injectable()
export class MySharedService {
    private dataSource = new BehaviorSubject<number>(0);
    private listVideoSource = new BehaviorSubject<any>([]);

    currentData = this.dataSource.asObservable();
    currentListVideo = this.listVideoSource.asObservable();

    constructor() { }

    changeData(data: number) {
        this.dataSource.next(data)
    }

    changeListVideo(listVideo: any) {
      this.listVideoSource.next(listVideo);
    }

}



