import { Component, OnInit } from '@angular/core';
import {VideoService} from '../../../shared/video.service';
import {Video} from '../../../models/video';
import {ActivatedRoute, Params, Router} from '@angular/router';
import {Helper} from "../../../common/helper";

@Component({
  selector: 'app-charts',
  templateUrl: './charts.component.html'
})
export class ChartsComponent implements OnInit {

  countrySupported = ['VN', 'US', 'JP', 'KR', 'CN'];
  videoList: any = null;
  country: string = '';
  constructor(private videoService: VideoService, private activatedRoute: ActivatedRoute, private router: Router, private helper: Helper) {}

  ngOnInit() {
    this.activatedRoute.params.subscribe(
      (params: Params) => {
        if (this.countrySupported.includes(params['code'])) {
          this.country = params['code'];
          this.callService();
        } else {
          //navigate 404
          this.router.navigate(['/trending/US']);
        }
      }
    );
  }

  callService() {
    this.videoService.fetchTrendingVideo(this.country)
      .subscribe(
        data => {
          this.videoList = data.items.map(
            item => {
              return new Video(
                item.id,
                item.snippet.title,
                item.snippet.thumbnails.high.url,
                item.snippet.description,
                item.statistics.likeCount,
                item.statistics.viewCount,
                item.snippet.channelTitle
              );
            }
          );
          this.helper.makeMeta('Trending Up in ' + this.country);
        }
      );
  }

  changeCountry(country: string) {
    console.log(country);
    this.country = country;
    this.callService();
  }

  changeVideoList(videoId: string) {
    this.helper.changeVideoList(videoId);
  }

}



