import { Component, OnInit, AfterViewInit  } from '@angular/core';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import {Video} from '../../../models/video';
import {VideoService} from '../../../shared/video.service';
import 'rxjs/add/operator/toPromise';
import {Router, ActivatedRoute, Params} from '@angular/router';
import {Helper} from "../../../common/helper";
import 'rxjs/add/operator/switchMap';
import {DownloaderService} from '../../../shared/downloader.service';
import {saveAs as importedSaveAs} from "file-saver";
declare var jquery: any;
declare var $: any;

@Component({
  selector: 'app-track-detail',
  templateUrl: './track-detail.component.html'
})
export class TrackDetailComponent implements OnInit  {

   videoId = '';
   player;
   ytEvent;
   videoList = [];
   activeVideo: Video;
   arrList = [];
   index = -1;
   pause: boolean = false;
   is_repeat: boolean = false;
   is_loaded: boolean = false;
   downLoadLink: any;
   is_loaded_download: boolean = false;

  constructor(private videoService: VideoService, private downloader: DownloaderService,
              private activatedRoute: ActivatedRoute, private router: Router, private helper: Helper) {
      this.activatedRoute.params.subscribe( params => {
          this.videoId = params['code'];
          this.fireVideo(this.videoId);
          this.initVideoList(this.videoId);
      } );
  }

  ngOnInit() {

  }


  repeatOneSong(){
    this.is_repeat = this.is_repeat ? false : true;
  }

  initVideoList(videoId: string) {
    this.videoService.fetchRelateVideo(videoId)
      .subscribe(data => {
        this.videoList = data.items.map(
          item => {
            return new Video(
              item.id.videoId,
              item.snippet.title,
              item.snippet.thumbnails.high.url,
              item.snippet.description, '', '',
              item.snippet.channelTitle);
          }
        );
        if (this.is_loaded) {
            this.videoList.push(this.activeVideo);
            console.log(this.videoList);
        }

      });
  }
  playList() {
      this.helper.changeVideoList(this.activeVideo.videoId);
  }


  fireVideo(videoId: string) {
    // this.safeURL = this.helper.makeUrlSafe(videoId);
    this.videoService.detailVideo(videoId)
      .subscribe(data => {
        let arrTmp = data.items.map(
          item => {
            return new Video(
              item.id,
              item.snippet.title,
              item.snippet.thumbnails.high.url,
              item.snippet.description,
              item.statistics.likeCount,
              item.statistics.viewCount,
              item.snippet.channelTitle);
          }
        );
        this.activeVideo = arrTmp[0];
        this.is_loaded = true;
        this.helper.makeMeta(this.activeVideo.title, this.activeVideo.description, this.activeVideo.thumbnail);
      });
  }

  changeVideoList(videoId: string) {
    this.helper.changeVideoList(videoId);
  }

}



