import { Injectable } from '@angular/core';
import {Http} from "@angular/http";
import 'rxjs/add/operator/map';

@Injectable()
export class VideoService {

  lyric: string = '';
  api_key = 'AIzaSyAARhzDEdAwaIYKelgTmVa8Nez5sLKjBcM';
  constructor(private http: Http) {

  }

  fetchVideos(query: string) {
    return this.http
      .get(`https://www.googleapis.com/youtube/v3/search?part=snippet&q=${query}` +
          '&maxResults=25' +
          '&type=video' +
          '&key=' + this.api_key)
      .map(response => response.json());
  }

  detailVideo(videoId: string) {
    return this.http
      .get(`https://www.googleapis.com/youtube/v3/videos?part=snippet%2CcontentDetails%2Cstatistics&id=${videoId}` +
          '&key=' + this.api_key)
      .map(response => response.json());
  }

  fetchRelateVideo(query: string) {
    return this.http
      .get(`https://www.googleapis.com/youtube/v3/search?part=snippet&relatedToVideoId=${query}` +
        '&maxResults=10' +
        '&type=video' +
        '&key=' + this.api_key)
      .map(response => response.json());
  }

  fetchTrendingVideo(country: string) {
    return this.http
      .get(`https://www.googleapis.com/youtube/v3/videos?part=snippet%2CcontentDetails%2Cstatistics&chart=mostPopular&regionCode=${country}` +
        '&maxResults=50' +
        '&key=' + this.api_key)
      .map(response => response.json());
  }

  fetchHotVideo(country: string, categoryId: number, limit: number = 20) {
      return this.http
          .get(`https://www.googleapis.com/youtube/v3/videos?part=snippet%2CcontentDetails%2Cstatistics&chart=mostPopular&regionCode=${country}` +
              '&videoCategoryId=' + categoryId +
              '&maxResults=' + limit +
              '&key=' + this.api_key)
          .map(response => response.json());
  }

  fetchLyric(artist: string, song: string) {
      const proxyurl = "https://cors-anywhere.herokuapp.com/";
      const url = "https://lyric-api.herokuapp.com/api/find/Charlie%20Puth/How%20Long"; // site that doesn’t send Access-Control-*
      return fetch(proxyurl + url) // https://cors-anywhere.herokuapp.com/https://example.com
          .then(response => response.text())
          .then(contents => this.lyric = contents);
  }
}



