import {ModuleWithProviders} from "@angular/core";
import {Routes, RouterModule} from "@angular/router";
import {PublicComponent} from "./layouts/public/public.component";
import {ChartsComponent} from "./components/public/charts/charts.component";
import {BrowseComponent} from "./components/public/browse/browse.component";
import {TrackDetailComponent} from "./components/public/track-detail/track-detail.component";
import {SearchComponent} from './components/public/search/search.component';
import {PlaylistComponent} from './components/public/playlist/playlist.component';
import {QueueComponent} from "./components/public/queue/queue.component";
import {DiscoveryComponent} from "./components/public/discovery/discovery.component";

export const ROUTE: Routes = [
  {
    path: '',
    redirectTo: 'chart',
    pathMatch: 'full'
  },
  {
    path: '',
    component: PublicComponent,
    children: [
      { path: '', component: DiscoveryComponent },
      { path: 'trending/:code', component: ChartsComponent },
      { path: 'browse', component: BrowseComponent },
      { path: 'video/:code', component: TrackDetailComponent },
      { path: 'playlist/:code', component: PlaylistComponent },
      { path: 'search', component: SearchComponent },
      { path: 'queue', component: QueueComponent },
      { path: 'discovery', component: DiscoveryComponent }
    ]
  }
];

export const routing: ModuleWithProviders = RouterModule.forRoot(ROUTE, {useHash: true});



