import { Component, OnInit, Input } from '@angular/core';
import {Video} from '../../../models/video';
import {VideoService} from '../../../shared/video.service';
import 'rxjs/add/operator/toPromise';
import {ActivatedRoute} from '@angular/router';
import {Helper} from "../../../common/helper";
import {MySharedService} from '../../../shared/MySharedService';

declare var jquery: any;
declare var $: any;

@Component({
  selector: 'embed-list-video',
  templateUrl: './embed-list.component.html'
})
export class EmbedListComponent implements OnInit {

  @Input() inputList: any;
  videoId = '';
  safeURL;
  player;
  ytEvent;
  activeVideo: Video;
  videoList = [];
  arrList = [];
  index = 0;
  pause: boolean = false;
  is_repeat: boolean = false;
  currentRoute: string = '';
  listLocal: any;
  deletedVideoId = '';
  query: string = '';
  searchVideoList: any = null;
  objTemp: any = {};

  constructor(private videoService: VideoService, private route: ActivatedRoute,
              private helper: Helper, private sharedService: MySharedService) {
  }

  ngOnInit() {
    this.initVideoList();
    this.currentRoute = this.route.snapshot.url[0].path;
    this.query = localStorage.getItem('search');
    this.search(this.query);
  }

    playList(){
      if (this.currentRoute === 'playlist') {
        console.log( this.route.snapshot.url);
        let code = this.route.snapshot.url[1].path;
        this.helper.changePlaylist(code);
      }
    }

  pauseAndPlay(){
    if (this.player.getPlayerState() == 1) {
      this.pause = true;
      this.pauseVideo();
    } else if (this.player.getPlayerState() == 2) {
      this.playVideo();
      this.pause = false;
    }
  }


  initVideoList() {
    this.arrList = this.inputList;
    this.videoId = this.arrList[0];
    this.fireVideo(this.videoId);
    for (let i = 0; i < this.arrList.length; i++) {
      this.videoService.detailVideo(this.arrList[i])
        .subscribe(data => {
          let arrTmp = data.items.map(
            item => {
              return new Video(
                item.id,
                item.snippet.title,
                item.snippet.thumbnails.high.url,
                item.snippet.description,
                item.statistics.likeCount,
                item.statistics.viewCount,
                item.snippet.channelTitle);
            }
          );
          this.videoList[i] = arrTmp[0];
          this.activeVideo = this.videoList[this.index];
        });
    }
  }



  onStateChange(event) {
    // video ended
    if (event.data === 0) {
      //Repeat case
      if (this.is_repeat) {
        this.reloadVideo(this.activeVideo.videoId);
        return false;
      }
      if (this.index === (this.videoList.length - 1)) {
        this.index = 0;
      } else {
        this.index = this.index + 1;
      }
      this.activeVideo = this.videoList[this.index];
      this.reloadVideo(this.activeVideo.videoId, 'no_index');
    }
    this.ytEvent = event.data;
  }

  savePlayer(player) {
    this.player = player;
    this.player.playVideo();
  }

  playVideo() {
    this.player.playVideo();
  }

  pauseVideo() {
    this.player.pauseVideo();
  }

  fireVideo(videoId: string) {
    // this.safeURL = this.helper.makeUrlSafe(videoId);
    this.videoService.detailVideo(videoId)
      .subscribe(data => {
        let arrTmp = data.items.map(
          item => {
            return new Video(
              item.id,
              item.snippet.title,
              item.snippet.thumbnails.high.url,
              item.snippet.description,
              item.statistics.likeCount,
              item.statistics.viewCount,
              item.snippet.channelTitle);
          }
        );
        this.activeVideo = arrTmp[0];
      });

  }

  reloadVideo(videoId: string, flag = '') {
    if (flag === '') {
      for (let i = 0; i < this.videoList.length; i++) {
        if (videoId === this.videoList[i].videoId) {
          this.index = i;
        }
      }
    }
    this.fireVideo(videoId);
    this.player.loadVideoById(videoId);
  }

  remove_item_queue(videoId: string){

    var data = [];
    var list = [];
    this.listLocal = JSON.parse(localStorage.getItem('queue'));
    for (let k in this.listLocal) {
      if (this.listLocal[k].id == videoId) {
        delete(this.listLocal[k]);
      } else {
        data.push(this.listLocal[k]);
      }
    }

    for (let j in this.videoList) {
      if (this.videoList[j].videoId == videoId) {
        // Delete first element
        if (j == "0") {
          this.index = -1;
        } else if (j == (this.videoList.length - 1).toString()) {
          this.index = 0;
        }
        delete(this.videoList[j]);
      } else {
        list.push(this.videoList[j]);
      }
    }
    $("#"+videoId).hide();
    this.videoList = list;
    localStorage.setItem('queue', JSON.stringify(data));
    this.sharedService.changeData(JSON.parse(localStorage.getItem('queue')).length);
    $.toast({
      heading: 'Success',
      text: 'Your queue has been updated.',
      showHideTransition: 'slide',
      icon: 'success',
      position: 'top-right'
    });
  }

  add2queue(videoId: string) {
    var data = [];
    var items = JSON.parse(localStorage.getItem('queue'));
    for (let i = 0; i < items.length; i++) {
      if (items[i].id === videoId) {
        $.toast({
          heading: 'Warning',
          text: 'This video is already queued.',
          showHideTransition: 'slide',
          icon: 'warning',
          position: 'top-right'
        });
        return false;
      }
      data.push(items[i]);
    }
    this.objTemp.id = videoId;
    data.push(this.objTemp);
    localStorage.setItem('queue', JSON.stringify(data));

    //Add to current list
    this.videoService.detailVideo(videoId)
      .subscribe(data => {
        let arrTmp = data.items.map(
          item => {
            return new Video(
              item.id,
              item.snippet.title,
              item.snippet.thumbnails.high.url,
              item.snippet.description,
              item.statistics.likeCount,
              item.statistics.viewCount,
              item.snippet.channelTitle);
          }
        );
        this.videoList.push(arrTmp[0]);
        console.log(arrTmp[0]);
      });


    $.toast({
      heading: 'Success',
      text: 'Add to queue sucessfully.',
      showHideTransition: 'slide',
      icon: 'success',
      position: 'top-right'
    });
  }

  search(query: string) {
    this.getFromLocal(query);
    this.videoService.fetchVideos(this.query)
      .toPromise()
      .then(data => {
        this.searchVideoList = data.items.map(item => {
          return new Video(
            item.id.videoId,
            item.snippet.title,
            item.snippet.thumbnails.high.url,
            item.snippet.description, '', '', item.snippet.channelTitle);
        });
      });
  }

  getFromLocal(query: string) {
    if (localStorage.getItem("search") === null || query === '') {
      localStorage.setItem('search', '');
    } else {
      localStorage.setItem('search', query);
    }
    this.query = localStorage.getItem('search');
  }

}



