import { Component, OnInit, Input } from '@angular/core';
import {PlaylistService} from '../../../shared/playlist.service';
import {Playlist} from '../../../models/playlist';
import {Helper} from '../../../common/helper';
@Component({
  selector: 'app-related-playlist',
  templateUrl: './related-playlist.component.html',
  styleUrls: ['./related-playlist.component.css']
})
export class RelatedPlaylistComponent implements OnInit {

  @Input() inputTitle;
  video_title: string = '';
    playlists: any;
    playListRelateisLoaded: boolean = false;
  constructor(private playlistService: PlaylistService, private helper: Helper) { }

  ngOnInit() {
    this.getRelatedPlaylist();
  }

  getRelatedPlaylist() {
    this.video_title = this.inputTitle;
      this.playlistService.fetchRelatedPlaylistInfoYoutube(this.video_title)
          .subscribe(
              data => {
                  this.playlists = data.items.map(
                      item => {
                          return new Playlist('',
                              item.id.playlistId,
                              item.snippet.title,
                              item.snippet.thumbnails.high.url,
                              item.snippet.channelTitle
                          );
                      });
                  console.log(this.playlists);
                  this.playListRelateisLoaded = true;
              });

  }

}



