import { Component, OnInit } from '@angular/core';
import {PlaylistService} from '../../../shared/playlist.service';
import {ActivatedRoute, Params} from '@angular/router';
import 'rxjs/add/operator/switchMap';
import {Helper} from '../../../common/helper';
declare var jquery: any;
declare var $: any;
@Component({
  selector: 'app-track-detail',
  templateUrl: './playlist.component.html'
})
export class PlaylistComponent implements OnInit  {

    inputList: string[] = [];
    is_loaded: boolean = false;
    code: string = '';

    constructor(private playlistService: PlaylistService, private route: ActivatedRoute, private helper: Helper) { }

    ngOnInit() {
        this.route.params.subscribe((params: Params) => {
            this.code = params['code'];
            this.loadListItem(this.code);
            this.loadDetailPlaylist(this.code);
        });
    }
    playList() {
        this.helper.changePlaylist(this.code);
    }

    loadListItem(code: string = '') {
        this.playlistService.fetchDetailPlaylistYoutube(code)
        .subscribe(data => {
                data.items.map(
                    item => {
                        this.inputList.push(item.contentDetails.videoId);
                    }
                );
                this.is_loaded = true;
            }
        );
    }

    loadDetailPlaylist(code: string = '') {
        this.playlistService.fetchPlaylistInfoYoutube(code)
        .subscribe(data => {
                data.items.map(
                    item => {
                        this.helper.makeMeta(item.snippet.title, item.snippet.description, item.snippet.thumbnails.high.url);
                    }
                );
            }
        );
    }

}



