import { Component, OnInit } from '@angular/core';
import {MySharedService} from '../../shared/MySharedService';

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html'
})
export class SidebarComponent implements OnInit {

  numbertrack = 0;
  inputList: any = [];
  constructor(private sharedService: MySharedService) { }

  ngOnInit() {
      if (localStorage.getItem('queue') !== null) {
          this.sharedService.changeData(JSON.parse(localStorage.getItem('queue')).length);
          this.sharedService.currentData.subscribe(data => this.numbertrack = data);
      }
    }

    playQueue() {
      this.inputList = [];
      var items = JSON.parse(localStorage.getItem('queue'));
      for (let i = 0; i < items.length; i++) {
        this.inputList.push(items[i].id);
      }
      this.sharedService.changeListVideo(this.inputList);
    }

}



