import { Injectable } from '@angular/core';
import { Http, Response, Headers } from '@angular/http';
import {Playlist} from '../models/playlist';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';
import { Functions } from '../common/functions';
import {API_Main} from '../config/API_Main';

@Injectable()
export class PlaylistService {

    private apiUrl = '';
    private headers = new Headers({'Content-Type': 'application/json'});

    private headerGet = this._func.AuthHeader();
    private headerPostJson = this._func.AuthHeaderPostJson();
    api_key = 'AIzaSyAARhzDEdAwaIYKelgTmVa8Nez5sLKjBcM';

    constructor(private _http: Http, private _func: Functions, private apiMain: API_Main) {
        this.apiUrl = this.apiMain.API_URL_PLAYLIST;
    }

    getData(page: number) {
        const url = `${this.apiUrl}?page=${page}`;
        return this._http.get(url, {headers: this.headerGet})
            .map((res: Response) => res.json());
    }

    showData(id: number) {
        const url = `${this.apiUrl}/${id}`;
        return this._http.get(url)
            .map((res: Response) => res.json());
    }

    addData(playlist: Playlist) {
        console.log(JSON.stringify(playlist));
        return this._http.post(this.apiUrl, JSON.stringify(playlist), {headers: this.headers})
            .map((res: Response) => res.json());
    }

    updateData(playlist: Playlist) {
        console.log(playlist);
        const url = `${this.apiUrl}/${playlist.id}`;
        return this._http.put(url, JSON.stringify(playlist), {headers: this.headers})
            .toPromise()
            .then((res: Response) => res.json());
    }

    deleteData(id: string) {
        const url = `${this.apiUrl}/${id}`;
        return this._http.delete(url).toPromise();
    }

    fetchPlaylistYoutube(country: string, keyword: string, limit: number = 40) {
        return this._http
            .get(`https://www.googleapis.com/youtube/v3/search?part=snippet&order=viewCount&type=playlist&q=${keyword}` +
                '&maxResults=' + limit +
                '&key=' + this.api_key)
            .map(response => response.json());
    }

    fetchDetailPlaylistYoutube(playlist_id: string) {
        return this._http
            .get(`https://www.googleapis.com/youtube/v3/playlistItems?part=snippet%2CcontentDetails&maxResults=10&playlistId=${playlist_id}` +
                '&key=' + this.api_key)
            .map(response => response.json());
    }

    fetchPlaylistInfoYoutube(playlist_id: string) {
        return this._http
            .get(`https://www.googleapis.com/youtube/v3/playlists?part=snippet%2CcontentDetails&maxResults=1&id=${playlist_id}` +
                '&key=' + this.api_key)
            .map(response => response.json());
    }

    fetchRelatedPlaylistInfoYoutube(video_title: string) {
        return this._http
            .get(`https://www.googleapis.com/youtube/v3/search?part=snippet&type=playlist&maxResults=8&q=${video_title}` +
                '&key=' + this.api_key)
            .map(response => response.json());
    }
}



