import { Component, OnInit } from '@angular/core';
import {VideoService} from '../../../shared/video.service';
import {Video} from '../../../models/video';
import {PlaylistService} from '../../../shared/playlist.service';
import {Playlist} from '../../../models/playlist';
import {Helper} from '../../../common/helper';
import {MySharedService} from '../../../shared/MySharedService';

@Component({
  selector: 'app-discovery',
  templateUrl: './discovery.component.html'
})
export class DiscoveryComponent implements OnInit {

  loaded_US: boolean = false;
  videoListRecommendUS: any = null;
  loaded_VN: boolean = false;
  videoListRecommendVN: any = null;
  loaded_KR: boolean = false;
  videoListRecommendKR: any = null;
  loaded_new_US: boolean = false;
  videoListNewUS: any = null;
  loaded_playlist: boolean = false;
  listPlaylist: any = null;
  playListCode: string = '';
  constructor(private videoService: VideoService, private playlistService: PlaylistService,
              private helper: Helper, private sharedService: MySharedService) { }

  ngOnInit() {
    this.getRecommendUS();
    this.getRecommendKR();
    this.getRecommendVN();
    this.getNewUS();
    this.getHotPlaylist();
    this.helper.makeMeta();
  }

  changePlaylist(playListCode: string) {
    this.helper.changePlaylist(playListCode);
  }

  changeVideoList(videoId: string) {
    this.helper.changeVideoList(videoId);
  }



  getHotPlaylist() {
      var tmp1 = ['vevo', 'top 10', 'today hit'];
      var code = tmp1[Math.floor(Math.random() * tmp1.length)];
      this.playlistService.fetchPlaylistYoutube('US', code, 5)
          .subscribe(
              data => {
                  this.listPlaylist = data.items.map(
                      item => {
                          return new Playlist('',
                              item.id.playlistId,
                              item.snippet.title,
                              item.snippet.thumbnails.high.url,
                              item.snippet.channelTitle
                          );
                      });
                  this.loaded_playlist = true;
              });
  }

  getNewUS() {
      this.videoService.fetchHotVideo('US', 10, 8)
          .subscribe(
              data => {
                  this.videoListNewUS = data.items.map(
                      item => {
                          return new Video(
                              item.id,
                              item.snippet.title,
                              item.snippet.thumbnails.high.url,
                              item.snippet.description,
                              item.statistics.likeCount,
                              item.statistics.viewCount,
                              item.snippet.channelTitle
                          );
                      }
                  );
                  this.loaded_new_US = true;
              }
          );
  }

  getRecommendUS() {
      this.videoService.fetchHotVideo('US', 10, 10)
          .subscribe(
              data => {
                  this.videoListRecommendUS = data.items.map(
                      item => {
                          return new Video(
                              item.id,
                              item.snippet.title,
                              item.snippet.thumbnails.high.url,
                              item.snippet.description,
                              item.statistics.likeCount,
                              item.statistics.viewCount,
                              item.snippet.channelTitle
                          );
                      }
                  );
                  this.loaded_US = true;
              }
          );
  }
  getRecommendVN() {
      this.videoService.fetchHotVideo('VN', 10, 10)
          .subscribe(
              data => {
                  this.videoListRecommendVN = data.items.map(
                      item => {
                          return new Video(
                              item.id,
                              item.snippet.title,
                              item.snippet.thumbnails.high.url,
                              item.snippet.description,
                              item.statistics.likeCount,
                              item.statistics.viewCount,
                              item.snippet.channelTitle
                          );
                      }
                  );
                  this.loaded_VN = true;
              }
          );
  }
  getRecommendKR() {
      this.videoService.fetchHotVideo('JP', 10, 10)
          .subscribe(
              data => {
                  this.videoListRecommendKR = data.items.map(
                      item => {
                          return new Video(
                              item.id,
                              item.snippet.title,
                              item.snippet.thumbnails.high.url,
                              item.snippet.description,
                              item.statistics.likeCount,
                              item.statistics.viewCount,
                              item.snippet.channelTitle
                          );
                      }
                  );
                  this.loaded_KR = true;
              }
          );
  }

}



