import { BrowserModule } from '@angular/platform-browser';
import { HttpModule } from '@angular/http';
import { NgModule } from '@angular/core';
import { YoutubePlayerModule } from 'ngx-youtube-player';
import {NgxPaginationModule} from 'ngx-pagination';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import {MySharedService} from './shared/MySharedService';

import { AppComponent } from './components/app/app.component';
import { SidebarComponent } from './inc/sidebar/sidebar.component';
import { ChartsComponent } from './components/public/charts/charts.component';
import { BrowseComponent } from './components/public/browse/browse.component';
import { PublicComponent } from './layouts/public/public.component';
import { SecureComponent } from './layouts/secure/secure.component';
import { AuthenticateComponent } from './layouts/authenticate/authenticate.component';

import {routing} from './app.routing';
import {Helper} from './common/helper';
import {Functions} from './common/functions';
import {API_Main} from './config/API_Main';

import {VideoService} from './shared/video.service';
import {PlaylistService} from './shared/playlist.service';
import {DownloaderService} from './shared/downloader.service';

import { ConvertNumberPipe } from './pipes/convert-number.pipe';
import {safeHtmlPipe} from './pipes/safeHtml.pipe';

import { FooterComponent } from './inc/footer/footer.component';
import { TrackDetailComponent } from './components/public/track-detail/track-detail.component';
import { SearchComponent } from './components/public/search/search.component';
import {PlaylistComponent} from './components/public/playlist/playlist.component';
import { QueueComponent } from './components/public/queue/queue.component';
import { EmbedListComponent } from './components/public/embed-list/embed-list.component';
import { DropdownBoxComponent } from './inc/dropdown-box/dropdown-box.component';
import { ConfirmDirective } from './confirm.directive';
import { HotComponent } from './components/public/hot/hot.component';
import { DiscoveryComponent } from './components/public/discovery/discovery.component';
import { AdsComponent } from './components/public/ads/ads.component';
import { RelatedPlaylistComponent } from './components/public/related-playlist/related-playlist.component';

@NgModule({
  declarations: [
    AppComponent,
    SidebarComponent,
    ChartsComponent,
    BrowseComponent,
    PublicComponent,
    SecureComponent,
    AuthenticateComponent,
    TrackDetailComponent,
    SearchComponent,
    FooterComponent,
    PlaylistComponent,
    QueueComponent,
    EmbedListComponent,
    DropdownBoxComponent,
    ConfirmDirective,
    HotComponent,
    DiscoveryComponent,
    ConvertNumberPipe,
    AdsComponent,
    RelatedPlaylistComponent,
    safeHtmlPipe
  ],
  imports: [
    BrowserModule,
    routing,
    HttpModule,
    YoutubePlayerModule,
    NgxPaginationModule,
    NgbModule.forRoot()
  ],
  providers: [VideoService, Helper, Functions, PlaylistService, API_Main, DownloaderService, MySharedService],
  bootstrap: [AppComponent]
})
export class AppModule { }



