import { Component, OnInit, Input } from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {MySharedService} from '../../shared/MySharedService';

declare var jquery:any;
declare var $ :any;

@Component({
  selector: 'dropdown-box',
  templateUrl: './dropdown-box.component.html'
})
export class DropdownBoxComponent implements OnInit {
  @Input() inputId: string;
  videoId: string;
  objTemp: any = {};
  items: any = {};
  currentRoute: string = '';
  inputList: any = [];

  constructor(private route: ActivatedRoute, private sharedService: MySharedService) { }

  ngOnInit() {
    this.videoId = this.inputId;
    this.currentRoute = this.route.snapshot.url.length > 0 ? this.route.snapshot.url[0].path : '';
  }

  add2queue(videoId: string) {
      // Create new object
      this.objTemp.id = videoId;
      this.objTemp.is_playing = 0;
      var data = [];
      if (localStorage.getItem('queue') === null) {
        data.push(this.objTemp);
        localStorage.setItem('queue', JSON.stringify(data));
      } else {
          var items = JSON.parse(localStorage.getItem('queue'));
          for (let i = 0; i < items.length; i++) {
              if (items[i].id === this.videoId) {
                  $.toast({
                    heading: 'Warning',
                    text: 'This video is already queued.',
                    showHideTransition: 'slide',
                    icon: 'warning',
                    position: 'top-right'
                  });
                  return false;
              }
              data.push(items[i]);
          }
          data.push(this.objTemp);
          localStorage.setItem('queue', JSON.stringify(data));
      }
      this.sharedService.changeData(JSON.parse(localStorage.getItem('queue')).length);

      $.toast({
        heading: 'Success',
        text: 'Add to queue sucessfully.',
        showHideTransition: 'slide',
        icon: 'success',
        position: 'top-right'
      });
  }

  add2playlist(videoId: string) {
    console.log(videoId);
  }

  editqueue(){

  }


}



