import { Component, OnInit, AfterViewInit } from '@angular/core';
import {VideoService} from '../../shared/video.service';
import {Video} from '../../models/video';
import 'rxjs/add/operator/toPromise';
import {ActivatedRoute} from '@angular/router';
import {Helper} from '../../common/helper';
import {MySharedService} from '../../shared/MySharedService';

declare var jquery: any;
declare var $: any;
declare var moment: any;

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.css']
})
export class FooterComponent implements OnInit {

  videoId = '';
  safeURL;
  player;
  ytEvent;
  activeVideo: Video;
  videoList = [];
  arrList = [];
  index = 0;
  pause: boolean = false;
  is_repeat: boolean = false;
  currentRoute: string = '';
  listLocal: any;
  deletedVideoId = '';
  query: string = '';
  searchVideoList: any = null;
  objTemp: any = {};
  inputList: any = [];
  showListItem: boolean = true;
  currentTime: number;
  durationTime: number;
  lyric: string = '';

  constructor(private videoService: VideoService, private route: ActivatedRoute,
              private helper: Helper, private sharedService: MySharedService) {

  }

  toHHMMSS(timeString: number) {
      var seconds = timeString;
      var duration = moment.duration(seconds, 'seconds');
      var formatted = duration.format("hh:mm:ss");
      if (formatted < 61) {
          formatted = '00:' + formatted;
      }
      return formatted;
  }

  ngOnInit() {
    this.sharedService.changeListVideo([]);
    this.sharedService.currentListVideo
      .subscribe(data => {
        this.inputList = data;
        this.initVideoList();
        this.currentRoute = this.route.snapshot.url.length > 0 ? this.route.snapshot.url[0].path : '';
      });
  }


  initVideoList() {
    this.arrList = this.inputList;
    if (this.arrList.length == 0) return;
    console.log(this.arrList);
    this.videoId = this.arrList[0];
    this.fireVideo(this.videoId);
    for (let i = 0; i < this.arrList.length; i++) {
      this.videoService.detailVideo(this.arrList[i])
        .subscribe(data => {
          let arrTmp = data.items.map(
            item => {
              return new Video(
                item.id,
                item.snippet.title,
                item.snippet.thumbnails.high.url,
                item.snippet.description,
                item.statistics.likeCount,
                item.statistics.viewCount,
                item.snippet.channelTitle);
            }
          );
          this.videoList[i] = arrTmp[0];
          this.activeVideo = this.videoList[0];
          this.player.loadVideoById(this.activeVideo.videoId);
        });
    }
  }

  pauseAndPlay(){
    if (this.player.getPlayerState() == 1) {
      this.pause = true;
      this.pauseVideo();
    } else if (this.player.getPlayerState() == 2) {
      this.playVideo();
      this.pause = false;
    }
  }

  repeatOneSong(){
    this.is_repeat = this.is_repeat ? false : true;
  }

  onStateChange(event) {
    // video ended
    if (event.data === 0) {
      //Repeat case
      if (this.is_repeat) {
        this.reloadVideo(this.activeVideo.videoId);
        return false;
      }
      if (this.index === (this.videoList.length - 1)) {
        this.index = 0;
      } else {
        this.index = this.index + 1;
      }
      this.activeVideo = this.videoList[this.index];
      this.reloadVideo(this.activeVideo.videoId, 'no_index');
    }
    this.ytEvent = event.data;
  }

  next() {
    if (this.index === (this.videoList.length - 1)) {
      this.index = 0;
    } else {
      this.index = this.index + 1;
    }
    this.activeVideo = this.videoList[this.index];
    this.reloadVideo(this.activeVideo.videoId, 'no_index');
  }

  previous() {
    if (this.index === 0) {
      this.index = this.videoList.length - 1;
    } else {
      this.index = this.index - 1;
    }
    this.activeVideo = this.videoList[this.index];
    this.reloadVideo(this.activeVideo.videoId, 'no_index');
  }

  savePlayer(player) {
    this.player = player;
    this.player.playVideo();
    setInterval(() => {
         this.currentTime = this.toHHMMSS(parseInt(this.player.getCurrentTime(), 10));
        this.durationTime = this.toHHMMSS(parseInt(this.player.getDuration(), 10));
        // this.currentTime = parseInt(this.player.getCurrentTime(), 10);
        // console.log(this.currentTime );
    }, 1000 );

    // this.durationTime = parseInt(this.player.getDuration(), 10);

  }

  playVideo() {
    this.player.playVideo();
  }

  pauseVideo() {
    this.player.pauseVideo();
  }

  displayListItem() {
    this.showListItem = !this.showListItem;
    this.showListItem ? $("#listItem").css("display", "block") : $("#listItem").css("display", "none");
  }

  fireVideo(videoId: string) {
    // this.safeURL = this.helper.makeUrlSafe(videoId);
    this.videoService.detailVideo(videoId)
      .subscribe(data => {
        let arrTmp = data.items.map(
          item => {
            return new Video(
              item.id,
              item.snippet.title,
              item.snippet.thumbnails.high.url,
              item.snippet.description,
              item.statistics.likeCount,
              item.statistics.viewCount,
              item.snippet.channelTitle);
          }
        );
        this.activeVideo = arrTmp[0];
        const proxyurl = "https://cors-anywhere.herokuapp.com/";
        const url = "https://lyric-api.herokuapp.com/api/find/Charlie%20Puth/How%20Long"; // site that doesn’t send Access-Control-*
        return fetch(proxyurl + url) // https://cors-anywhere.herokuapp.com/https://example.com
            .then(response => response.json())
            .then(contents => {this.lyric = contents.lyric.replace(/\n/g,'<br>');});
      });
  }

  reloadVideo(videoId: string, flag = '') {
    if (flag === '') {
      for (let i = 0; i < this.videoList.length; i++) {
        if (videoId === this.videoList[i].videoId) {
          this.index = i;
        }
      }
    }
    this.fireVideo(videoId);
    this.player.loadVideoById(videoId);
  }

}



